.burger {
  cursor: pointer;
  width: 25px;
  height: 16px;
  line-height: 1;
  position: relative;
  transition: all 0.25s ease-in-out;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  margin-right: 22px;
}

.burger__inner {
  margin-bottom: -3px;
}

.burger__line {
  width: 25px;
  height: 2px;
  background-color: #616161;
  position: relative;
  transition: all 0.25s ease-in-out;
  margin-bottom: 5px;
}

.burger__line_1 {
  transition: all 0.25s ease-in-out;
}

.burger__line_2 {
  transition: all 0.25s ease-in-out;
}

.burger__line_3 {
  transition: all 0.25s ease-in-out;
}

.burger__line_4 {
  transition: all 0.25s ease-in-out;
}

.burger.open .burger__line_1 {
  transform: translateY(8px) rotate(45deg);
}

.burger.open .burger__line_2 {
  opacity: 0;
}

.burger.open .burger__line_3 {
  opacity: 0;
}

.burger.open .burger__line_4 {
  transform: translateY(-7px) rotate(-45deg);
}

.logo-m {
  margin-right: 10px;
}

.logo-m__link {
  text-indent: -999999px;
  display: block;
  background-size: contain;
  max-width: 188px;
  border: none;
  text-decoration: none;
}

.search-m-btn__link {
  width: 21px;
  height: 21px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
  border-bottom: none;
}

.search-m-btn__link:hover path {
  stroke: #ED4545;
}

.phone-m-btn__link {
  display: flex;
  width: 22px;
  height: 22px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 18px;
  border-bottom: none;
}

.phone-m-btn__link:hover path {
  stroke: #ED4545;
}

.hd-m {
  display: none;
  height: 58px;
  padding-left: 18px;
  padding-right: 18px;
  border-bottom: 1px solid #D6D6D6;
}

.hd-m .dealer {
  padding: 0;
  margin: 0;
}

.hd-m .dealer:before {
  display: none;
}

.hd-m__inner {
  width: 100%;
  z-index: 2;
  transition: all 0.25s;
  display: flex;
  align-items: center;
}

.hd-m__cont {
  display: flex;
  align-items: center;
  flex-grow: 1;
  margin-right: 10px;
}

.hd-m__phone a {
  width: 45px;
  height: 45px;
  text-indent: -999999px;
  display: block;
  position: relative;
}

.hd-m__phone a:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  margin: auto;
}

.hd-m__search-m {
  width: 45px;
  height: 45px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 0 0 auto;
}

.hd-m__search-m span {
  position: relative;
}

@media only screen and (min-width: 768px) {
  .hd-m {
    padding-left: 27px;
    padding-right: 27px;
  }
}

@media only screen and (max-width: 1023px) {
  .hd-m {
    display: flex;
  }
}